.NavSuper {
  display: flex;
  flex-direction: row;
  height: 70px;
  width: 100%;
  justify-content: center;
  background-color: rgb(84, 14, 114);
  font-size: 1rem;
  color: orange;
  text-decoration: none;
  border-top: solid black 5px;
  border-bottom: solid black 5px;
  z-index: 20;

  /* border: solid 1px red; */
}

.NavContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 90%;
}

.Navbox {
  display: flex;
  flex-direction: row;
  align-items: center;

  /* border: black solid 1px; */
}

.Navlogo {
  display: flex;
  align-items: center;
  width: 4.5%;
}

.NavRightPanel {
  display: flex;
  flex-direction: row;
  justify-content: left;
  width: 70%;
}

.Navegacion {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 48%;
  padding: 0rem 3rem;
}

.Desplegable {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 0rem 1rem;
  width: 13%;
}

.MenuBtn {
  height: 40px;
  background-color: orange;
  border-radius: 10px;
  border: 4px solid #000000;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  color: rgb(84, 14, 114);
  font-family: "Patrick Hand", cursive;
  font-size: 28px;
  padding: 16px 31px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #2f6627;
  transition: ease-out 0.3s;
}
.MenuBtn:hover {
  background-color: #7cff3a;
}
.MenuBtn:active {
  position: relative;
  top: 1px;
}

.Social {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0rem 1rem;
  width: 8%;
}

.Navmint {
  display: flex;
  align-items: center;
  padding: 0rem 1rem;
  height: 100%;
  width: 10%;
}

.NavbtnCTA {
  width: 110px;
  height: 55px;
  border: solid #000000 4px;
  font-family: "Patrick Hand", cursive;
  font-weight: bolder;
  color: rgb(84, 14, 114);
  background-color: rgb(0, 255, 72);
  border-radius: 2rem;
  box-shadow: inset 0 0 0 0 rgb(242, 184, 49);
  transition: ease-out 0.3s;
  font-size: 30px;
  outline: none;
  cursor: pointer;
  animation: infinite 1s ease-in-out;
  animation-name: btnclickme;
}

@keyframes btnclickme {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.NavbtnCTA:hover {
  /* color: rgb(30, 112, 35); */
  background-color: yellow;
  /* border: solid rgb(45, 255, 90) 4px; */
}
